}

.subscribe__input:not(:placeholder-shown):invalid {
  border-color: var(--error-color);
}

.subscribe__input:not(:placeholder-shown):invalid + .label__error {
  visibility: visible;
  opacity: 1;
  transform: rotateX(0) translateY(0%);
}

.subscribe__input:not(:placeholder-shown):required:valid {
