  opacity: 0;
  transition: var(--transition-time-func);
  border-radius: 30px;
  justify-content: center;
  padding: 6px 23px;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
  transform: rotateX(90deg)translateY(-50%);
}

.error-icon {
  width: 12px;
  height: 12px;
  stroke-width: 0;
  fill: currentColor;
  margin-right: 4px;
  display: inline-block;
}

.error-message {
  font-size: 10px;
  font-weight: 400;
  line-height: 1.5;
}

.button {
  min-width: 102px;
  min-height: 42px;
