.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .3);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.modal {
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 525px;
  text-align: center;
  background-color: rgba(193, 205, 57, .9);
  border-radius: 25px;
  padding: 30px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px #b6b4b4, 0 1px 1px rgba(166, 163, 163, .14), 0 2px 1px rgba(184, 181, 181, .2);
}

@media screen and (min-width: 1200px) {
