}

.button-close {
  width: 30px;
  height: 30px;
  fill: var(--header-background-color);
  cursor: pointer;
  transition: var(--transition-time-func);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 50%;
